const Container = styled.div`
  /* background: #d1d3a2; */
  /* background: #d1dfff; */
  background: #282c34;
`

const Container2 = styled.div`
  /* background: #d1d3a2; */
  /* background: #d1dfff; */
  background: #282c34;
  overflow:auto;
  /* height: 800px; */
  height: 85vh;
  margin-top:10px;
`

const InputGroup = styled(Input.Group)`
margin: 1%;
`
