import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Input, message, Switch, Button, List, Dropdown, Menu, Modal, DatePicker } from 'antd';
import stores from '../stores';
import { Task } from '../model/model'
import { StoreModel, Duration } from '../model/model'
import Utils from '../utils/Utils'
import DateUtils from '../utils/DateUtils'
import TaskInfoFormat from '../utils/TaskInfoFormat'
import Format from '../utils/Format'
import { CurrentTaskKey } from '../stores/CurrentTaskStore';
import 'antd/dist/antd.css';
import { CurrentDoingTaskKey } from '../stores/CurrentDoingTaskStore';
import { ClickParam } from 'antd/lib/menu';
// import { DateType } from 'antd/lib/date-picker';
import { DownOutlined } from '@ant-design/icons';
import moment from 'moment'

const Container = styled.div`
  background: #ffffff;
  display: flex;
  height: 100%;
  flex-direction: column;
`

const Div = styled.div`
`

const Editor = styled(Input.TextArea)`
  flex-grow: 1;
`

const ButtonGroup = styled(Button.Group)`
margin: 2px;
`

const ContentLayout: React.FC = () => {
  const { storeModel } = stores.useStore('mainmodel') as { storeModel: StoreModel };
  const { currentTask } = stores.useStore(CurrentTaskKey) as { currentTask: Task | undefined }
  const { save } = stores.useStore('mainmodel') as { save: () => void };
  const [content, setContent] = useState("")
  const [expectTime, setExpectTime] = useState(-1);
  const [deadline, setDeadlineUI] = useState<any>(null);

  const [isPending, setPendingUI] = useState(false);
  const [pendingReason, setPendingReason] = useState("");
  const [pendingTime, setPendingTimeUI] = useState<any>(null);


  // toolsbar
  const { currentDoingTask, setCurrentDoingTask } = stores.useStore(CurrentDoingTaskKey) as { currentDoingTask: Task | undefined, setCurrentDoingTask: (note: Task | undefined) => void }

  /* ---------------------------åˆå§‹åŒ–------------------------------- */

  // æ¸²æŸ“å½“å‰note
  const [lastTask, setLastTask] = useState<Task | undefined>(undefined)
  useEffect(() => {
    // console.log(`Current content:${content}`)
    if (currentTask === undefined) return

    if (!currentTask.isSameTask(lastTask)) {
      // todo è¿™é‡Œæ”¾åŒæ­¥åº”è¯¥ä¸ä¼šæœ‰é—®é¢˜å§ï¼Œæœ€å¤§çš„é—®é¢˜åº”è¯¥æ˜¯å‰é¢åˆ‡äº†noteä½†æ²¡åˆ‡contentçš„æ—¶å€™è¿›è¡Œä¿å­˜...
      setLastTask(currentTask)
      setContent(currentTask.note.content)

      // todo è¿™éƒ¨åˆ†é€»è¾‘æŠ½å‡ºå»
      const expectTime = currentTask.getActualExpectConsume()
      if (expectTime == -1) {
        setExpectTime(expectTime)
      } else {
        setExpectTime(expectTime / (3600 * 1000))
      }

      setPendingUI(currentTask.isPending())
      setPendingReason(currentTask.pendingReason)
      setPendingTimeUI(getPendingDeadlineTime(currentTask))

      setDeadlineUI(getDeadlineTime(currentTask))

      storeModel.orderTasks()
    }
  })

  /* ----------------------------ä¿å­˜Noteæ–‡æœ¬------------------------------ */

  function onBlur(event: any) {
    event.preventDefault();
    saveToLocal(event, false)
  }

  function saveToLocal(event: any, isManual: boolean) {
    if (event) event.preventDefault();
    if (currentTask === undefined) return

    currentTask.note.content = content
    save()

    if (isManual) message.info("å·²ä¿å­˜")
  }

  /* ------------------------------toolsbar---------------------------- */

  function deleteTask(event: any) {
    if (currentTask === undefined) return

    Modal.confirm({
      title: 'ç¡®å®šè¦åˆ é™¤å—ï¼Ÿ',
      content: `æ˜¯å¦åˆ é™¤ä»»åŠ¡ï¼š${currentTask.title}`,
      onOk() {
        if (storeModel.removeTask(currentTask)) {
          message.success("åˆ é™¤ä»»åŠ¡æˆåŠŸ")
          save()
        } else {
          message.error("åˆ é™¤ä»»åŠ¡å¤±è´¥")
        }
      },
    });
  }

  function doneTask(event: any) {
    if (currentTask === undefined) return
    const task = currentTask

    if (task.isDone()) {
      message.error(`ä»»åŠ¡å·²å®Œæˆï¼Œå®Œæˆæ—¶é—´:${Format.formatTimeInMs(task.doneTime)}`)
      return
    }

    if (!task.isDoing()) {
      Modal.confirm({
        title: 'å½“å‰ä»»åŠ¡ä¸åœ¨è¿›è¡Œä¸­ï¼Œç¡®å®šè¦å®Œæˆå—ï¼Ÿ',
        content: `æ˜¯å¦å®Œæˆä»»åŠ¡ï¼š${task.title}`,
        onOk() {
          _realDone(task)
        },
      });
      return
    }

    task.stop()
    setCurrentDoingTask(undefined)
    _realDone(task)
  }

  function _realDone(task: Task) {
    task.done()
    storeModel.orderTasks()
    message.success("ä»»åŠ¡å·²å®Œæˆ")
    save()
  }

  function onChooseDeadline(value: any) {
    if (currentTask === undefined) return
    const task = currentTask

    // æ•´äº†åŠå¤©ï¼ŒåŸæ¥ç›´æ¥ valueOf() å°±è¡Œï¼Œç™¾åº¦äº†åŠå¤©
    // if(value instanceof Moment){
    //   let moment:Moment = value
    //   console.log('onOk: ', moment.);
    // }
    // console.log('onOk: ', value.valueOf());

    task.deadlines.push(value.valueOf())
    setDeadlineUI(getDeadlineTime(currentTask))
    save()
  }

  function getDeadlineTime(task: Task): any {
    const deadline = task.getActualDeadline();
    if (deadline > 0) {
      return moment(deadline)
    }

    return null
  }

  function getShowTips(): string {
    if (currentTask === undefined) return ""
    const task = currentTask

    let result = "  " + TaskInfoFormat.getDoingStatusTips(task) + TaskInfoFormat.getConsumed(task) + TaskInfoFormat.getDeadline(task)  + TaskInfoFormat.getConsumeExpected(task)
    return result
  }

  function changeExceptTime() {
    if (currentTask === undefined) return
    const task = currentTask

    if (expectTime == -1) {
      task.expectConsumes.push(-1) // todo æš‚æ—¶ä¸ä¼˜åŒ–äº¤äº’äº†ï¼Œ-1å°±æ˜¯ä¸è®¾ç½®æ—¶é—´
    } else {
      task.expectConsumes.push(expectTime * 3600 * 1000)
    }
  }

  /* ---------------------------------------------------------- */

  function onChoosePendingDeadline(value: any) {
    if (currentTask === undefined) return
    const task = currentTask

    if (!isPending) {
      message.error(`è®¾ç½®æ— æ•ˆï¼Œè¯·æ‰“å¼€ pending å¼€å…³åé‡æ–°è®¾ç½®`)
      return
    }

    const pendingLine = value.valueOf()
    const deadline = task.getActualDeadline()
    if ( deadline> 0 && pendingLine > deadline) {
      message.error(`Pendingæ—¶é—´(${Format.formatTimeInMinute(pendingLine)})æ™šäºDeadline(${Format.formatTimeInMinute(deadline)})ï¼Œè¯·é‡æ–°è®¾ç½®`)
      return
    }

    task.pendingDeadline = pendingLine
    setPendingTimeUI(getPendingDeadlineTime(task))
    save()
  }

  function getPendingDeadlineTime(task: Task): any {
    const deadline = task.pendingDeadline

    if (deadline > 0) {
      return moment(deadline)
    }

    return null
  }

  function savePendingStatus() {
    if (currentTask === undefined) return
    const task = currentTask

    task.pendingReason = pendingReason
    save()
  }

  function setPending(pendingStatus: boolean) {
    if (currentTask === undefined) return
    const task = currentTask

    if (!pendingStatus) {
      task.pendingDeadline = -1
    }
    setPendingUI(pendingStatus)
    save()
  }

  /* ---------------------------------------------------------- */

  return (
    <Container>
      {/* todo æ²¡æœ‰ currentTask å°±ä¸å±•ç¤º */}

      {/* ä¸åŠ è¿™ä¸ª div ä¼šæ¢è¡Œ */}
      <Div>
        <ButtonGroup>
          {/* ç‚¹å‡»æ•´è¡Œå°±æ˜¯æŸ¥çœ‹ä¸”å¼€å§‹ï¼Œviewæ˜¯åªæŸ¥çœ‹ä¸å¼€å§‹ã€‚æš‚æ—¶ç”¨ä¸ç€å§ */}
          {/* <Button type="primary" size="small">view</Button> */}
          <Button onClick={(e) => deleteTask(e)}>D</Button>
          {/* <Button type="primary" size="small" onClick={(e) => startTask(e, task)}>Stop</Button> */}
          <Button onClick={(e) => doneTask(e)}>Done</Button>

        </ButtonGroup>

        <DatePicker showTime value={deadline} onOk={value => onChooseDeadline(value)} />
        {getShowTips()}

        <Input style={{ width: 'auto' }} value={expectTime} placeholder="èŠ±è´¹æ—¶é—´ å›è½¦ä¿å­˜" prefix="é¢„è®¡" suffix="h" onChange={(event) => setExpectTime(Number(event.target.value))} onPressEnter={changeExceptTime} />
      </Div>

      <Div>
        <Switch checkedChildren="Pending" unCheckedChildren="No Pending" checked={isPending} onChange={(event) => setPending(event)} />

        <DatePicker showTime value={pendingTime} onOk={value => onChoosePendingDeadline(value)} />

        <Input style={{ width: 'auto' }} value={pendingReason} prefix="åŸå› :" placeholder="è‡ªåŠ¨ä¿å­˜" onChange={(event) => setPendingReason(event.target.value)} onBlur={savePendingStatus} />
      </Div>

      <Editor
        placeholder="ä»»åŠ¡æ—¥å¿—ï¼ˆè‡ªåŠ¨ä¿å­˜ï¼‰" value={content} onChange={(event) => setContent(event.target.value)} onBlur={(e) => onBlur(e)}
      />
    </Container>
  );
}

export default ContentLayout;